/*# sourceMappingURL=bootstrap-grid.min.css.map */
.t1 {
    font-size: 29px;
    line-height: 29px;
    margin-bottom: 10px;
}

.t2 {
    font-size: 24px;
    line-height: 24px;
    margin-bottom: 5px;
}

p {
    font-family: "Roboto", sans-serif;
    font-size: 16px;
    color: #676767;
    margin-top: 0;
}

.container {
    padding: 20px;
}

.icon-back {
    margin-top: 10px;
    width: 30px;
}

input {
    appearance: none;
    -webkit-appearance: none;
    width: 100%;
    padding: 15px;
    border: 1px solid #BFBFBF;
    border-radius: 8px;
    font-family: "Roboto", sans-serif;
    font-size: 16px;
    margin-bottom: 15px;
}
input.search-fixed-input {
    border-radius: 80px;
    margin-bottom: 0;
    margin-bottom: 0;
}

button[type=submit] {
    width: 100%;
    padding: 13px 0 18px;
    appearance: none;
    -webkit-appearance: none;
    font-size: 19px;
    border: 0;
    border-radius: 30px;
    background: #194E64;
    color: #FFF;
    font-family: "mabry_proregular";
    margin-top: 15px;
}

.button {
    width: 100%;
    padding: 15px 0 15px;
    font-size: 19px;
    border: 0;
    border-radius: 30px;
    color: #FFF;
    font-family: "mabry_proregular";
    margin-top: 15px;
    text-align: center;
    display: block;
    background: #BEBEBE;
}
.button.spotify {
    background: #44B354;
}
.button.spotify span {
    position: relative;
}
.button.spotify span:before {
    content: "";
    width: 25px;
    height: 25px;
    vertical-align: middle;
    margin-left: -40px;
    background: url("../img/icon-spotify.png");
    position: absolute;
    background-size: 100%;
    margin-right: 100%;
}

.text-right {
    text-align: right;
}

body {
    font-family: "mabry_proregular";
}

.logo {
    width: 60%;
    margin: 0 auto;
    padding: 4vh 0;
}
.logo img {
    width: 100%;
}

.container-connexion {
    background: url("../img/bg/connexion.png");
    background-size: cover;
    padding-bottom: 15px;
    margin-bottom: 15px;
}

.forget-password {
    text-align: right;
}
.forget-password a {
    color: #676767;
    text-decoration: underline;
}

.hero {
    border-bottom: 1px solid #F0F0F0;
}
.hero.search {
    position: fixed;
    width: 100%;
    z-index: 30;
    left: 0;
    top: 0;
    background: #FFF;
}
.hero.search .container {
    padding: 14px 20px;
}

.info-user {
    display: inline-block;
    vertical-align: middle;
}
.info-user .user-name {
    font-size: 16px;
}
.info-user .link-account {
    font-family: "Roboto", sans-serif;
    font-size: 12px;
    color: #676767;
}

.img-user {
    display: inline-block;
    width: 40px;
    height: 40px;
    vertical-align: middle;
    overflow: hidden;
    border-radius: 50%;
    margin-left: 10px;
}
.img-user img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.container-search-form {
    padding: 15px 0;
    border-bottom: 1px solid #F0F0F0;
}

.search-fixed {
    width: calc(100% - 45px);
    display: inline-block;
    margin-left: auto;
    float: right;
}

.container-playlist .container {
    padding-bottom: 0;
    padding-top: 30px;
}

.playlists {
    max-width: 100%;
    overflow: auto;
    white-space: nowrap;
    padding-left: 20px;
}
.playlists a {
    color: #676767;
}
.playlists li {
    white-space: nowrap;
    display: inline-block;
    width: 60%;
}
.playlists li .container-playlist {
    white-space: nowrap;
    display: inline-block;
    padding-right: 20px;
    vertical-align: top;
    width: 100%;
}
.playlists li .container-playlist .container-image {
    width: 100%;
    height: calc(60vw - 20px);
    overflow: hidden;
}
.playlists li .container-playlist img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.playlists li .name-playlist {
    white-space: normal;
    font-size: 14px;
    padding-top: 15px;
}

.on-load {
    position: fixed;
    width: 100%;
    display: none;
    height: 100%;
    background: rgba(255, 255, 255, 0.8);
}

.list-tracks {
    padding-top: 82px;
}
.list-tracks li {
    padding: 10px 20px;
    border-bottom: 1px solid #F0F0F0;
}
.list-tracks li a {
    color: #676767;
}
.list-tracks li .track-info {
    width: calc(83vw - 50px);
    display: inline-block;
    padding-left: 3vw;
    font-family: "Roboto";
}
.list-tracks li .track-info .title-track {
    font-size: 16px;
    line-height: 18px;
    margin-top: 2px;
    color: #676767;
}
.list-tracks li .track-info .track-author {
    font-size: 12px;
}
.list-tracks li .counter {
    width: 7vw;
    display: inline-block;
    font-size: 13px;
    margin-top: 10px;
    vertical-align: top;
}
.list-tracks li .container-image-list {
    display: inline-block;
    width: 10vw;
    height: 10vw;
    overflow: hidden;
    vertical-align: top;
}
.list-tracks li .container-image-list img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.track-add {
    position: fixed;
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.9);
    z-index: 5;
    left: 0;
    top: 0;
    text-align: center;
    display: none;
}
.track-add .content-track-add {
    position: absolute;
    top: 50%;
    width: 100%;
    transform: translateY(-50%);
    padding: 0 40px;
}
.track-add .content-track-add img {
    display: block;
    margin: 0 auto 30px;
    width: 60px;
}

.container-img-playlist {
    position: relative;
    margin: 0 auto;
    height: 50vh;
    background-size: contain;
    background-position: top left;
    background-attachment: fixed;
}
.container-img-playlist:after {
    content: "";
    width: 100%;
    height: 100%;
    background: linear-gradient(0deg, white 0%, rgba(255, 255, 255, 0.9) 50%, rgba(255, 255, 255, 0.6) 100%);
    position: absolute;
    left: 0;
    top: 0;
}
.container-img-playlist img {
    opacity: 0.4;
}
.container-img-playlist .container-info-playlist {
    position: absolute;
    bottom: 0;
    margin-bottom: 20px;
    left: 20px;
    width: calc(100% - 40px);
    z-index: 3;
}
.container-img-playlist .container-info-playlist p {
    margin-bottom: 5px;
}

.list-tracks.playlist {
    padding-top: 0;
}

.hero.playlist .t2 {
    text-align: center;
    margin-top: 8px;
}
.hero.playlist .icon-back {
    margin-top: 5px;
}

.loading {
    width: 50px;
    height: 50px;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -25px;
    border: 4px solid #f3f3f3;
    border-top: 4px solid #ff5722;
    border-radius: 50%;
    animation: spin 1.5s linear infinite;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}